import Vue from 'vue'
import Router from 'vue-router'
const _import = require('./_import_' + process.env.NODE_ENV)
// 页面导入
const _importPage = file => _import('screens/' + file)

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    name: 'screen',
    redirect: '/login'
  },
  {
    path: '/login',
    name: 'login',
    component: _importPage('login/index'),
    meta: {
      title: 'nb-lot数据大脑登陆'
    }
  }]
})
let hasAddrouter = false
//路由拦截
router.beforeEach((to, from, next) => {
  // if(to.name!=='login')
  if (/login/ig.test(to.path)) {
    next()
  } else {
    console.log(sessionStorage.getItem('user'))
    if (sessionStorage.getItem('user')) {
      if (!hasAddrouter) {
        router.addRoutes([{
          path: '/nb-iot',
          name: 'nb-iot',
          component: _importPage('nb-lot/index'),
          meta: {
            title: 'nb-lot数据大脑'
          }
        }])
        hasAddrouter = true
        next({ ...to, replace: true })
      } else {
        next()
      }
    } else {
      next('/login')
    }
  }
  // console.log(to,from,next)
})
export default router
